import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import { ButtonBase, Typography } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,
        padding: theme.spacing(4),
    },
    control: {
        padding: theme.spacing(2),
    },
    paper: {
        padding: theme.spacing(2),
        margin: 'auto',
        maxWidth: 500,
    },
    image: {
        width: '100%',
        height: '100%',

    },
    img: {
        margin: 'auto',
        // display: 'block',
        maxWidth: '100%',
        maxHeight: '100%',
        borderRadius: '1em'
    },
    name: {
        fontSize: '25px',
        color: 'gray',
        textAlign: "center",
        fontWeight: 'bold'
    },
    profession: {
        color: 'gray',
        textAlign: "center",
        fontWeight: 'bold'
    },
    team: {
        paddingTop: theme.spacing(5),
        textAlign: "center",
        color: 'gray',
        fontSize: '30px',
        fontWeight: 'bold'
    },
    profile: {
        backgroundColor: '#e3f2fd',

        paddingTop: theme.spacing(5)
    }
}));

function ManagementProfile() {
    const [spacing] = React.useState(2);
    const classes = useStyles();

    return (

        <div className={classes.profile}>
            {/* <Grid container className={classes.root} spacing={2}>
                <Grid item xs={12}>
                    <Grid container spacing={spacing}>
                        {[1, 2].map(value => (
                            <Grid key={value} item>
                                <Paper className={classes.paper} />
                            </Grid>
                        ))}
                    </Grid>
                </Grid>
            </Grid> */}
            <Typography className={classes.team}>Our management team</Typography>

            <div className={classes.root}>
                <Paper className={classes.paper}>
                    <Grid container spacing={2}>
                        <Grid item>
                            <ButtonBase className={classes.image}>
                                <img className={classes.img} src={process.env.PUBLIC_URL + 'shahzad.jpg'} />
                            </ButtonBase>
                        </Grid>
                        <Grid item xs={12} sm container>
                            <Grid item xs container direction="column" spacing={2}>
                                <Grid item xs>
                                    <Typography className={classes.name}>Arshad Ali</Typography>
                                    <Typography className={classes.profession}>( Managing Director )</Typography>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </Paper>
            </div>


            {/* <ImageBackground
                source={require('../../static/images/shahzad.jpg')}
                style={styles.container}>
                <View style={styles.overlay}></View>
                <Text style={[styles.textStyle, { paddingTop: 10 }]} >My Account</Text>
                <Image source={require('../images/lawrance.jpg')}
                    style={styles.avatarStyle} />
                <Text style={styles.textStyle} > Jenifer Lawrance</Text>
                <Text style={styles.textStyle} > +14155552671</Text>
                <Balance style={styles.balanceContainer} />
            </ImageBackground> */}
        </div>
    );
}

export default ManagementProfile;