import React from 'react';
import './About.css';
import Container from '@material-ui/core/Container';
import { Typography, makeStyles } from '@material-ui/core';


const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(2, 2),
        width: '100%'
    },
    how: {
        color: 'gray',
        textAlign: "center",
    },
    headerbody: {
        paddingLeft: '5px',
        paddingRight: '0px',
        margin: '0px',
        textAlign: "center"
    }
}));

export default function Header() {
    const classes = useStyles();
    return (
        <Container maxWidth="lg">
            <div className="aboutus">
                <h3 >Who are Ever Safe Packers and Movers ?</h3>
                <Typography className={classes.headerbody}> We are a technology driven company focussed on providing quality, reliable packing and moving services in India to our constomers.</Typography>
            </div>

            {/* <Paper className={classes.root}>
                <h2 className={classes.how}>Who are Ever Safe Packers and Movers ?</h2>
            </Paper> */}
        </Container>
    );
}