import React, { useEffect } from 'react';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import CssBaseline from '@material-ui/core/CssBaseline';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { Link } from "react-router-dom"

import axios from 'axios';
// import AppBar from '@material-ui/core/AppBar';
// import Button from '@material-ui/core/Button';
// import CameraIcon from '@material-ui/icons/PhotoCamera';
// import CardActions from '@material-ui/core/CardActions';
// import Toolbar from '@material-ui/core/Toolbar';
// import Link from '@material-ui/core/Link';
// import Footer from '../Footer/Footer';
// import StickyFooter from '../Footer/StickyFooter';
import Content from "../../content"
import { Button } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    icon: {
        marginRight: theme.spacing(2),
    },
    heroContent: {
        backgroundColor: theme.palette.background.paper,
        padding: theme.spacing(8, 0, 6),
    },
    heroButtons: {
        marginTop: theme.spacing(4),
    },
    cardGrid: {
        paddingTop: theme.spacing(8),
        paddingBottom: theme.spacing(8),
    },
    card: {
        height: '100%',
        display: 'flex',
        flexDirection: 'column',
    },
    cardMedia: {
        paddingTop: '56.25%',
    },
    cardContent: {
        flexGrow: 1,
    },
    footer: {
        backgroundColor: theme.palette.background.paper,
        padding: theme.spacing(6),
    },
}));

const cards = [1, 2, 3, 4, 5, 6, 7, 8];


export default function Album(props) {
    console.log(props)
    const classes = useStyles();
    const navigateTo = (id) => {
        console.log("clicked")
        props.history.push(`/gallery/${id}`)
    }
    const enquiries = [];

    useEffect(() => {
        axios.get('https://jsonplaceholder.typicode.com/photos').then((response) => {
            console.log('====================================');
            console.log("Response from API =>" + JSON.stringify(response));
            console.log('====================================');
        });
    }, []);

    return (
        <React.Fragment>
            <CssBaseline />
            <main>
                <div className={classes.heroContent}>
                    <Container maxWidth="lg">
                        {/* <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
                            Gallery
                        </Typography> */}
                        <Typography variant="h5" align="left" color="textSecondary" >
                            Something short and leading about the collection belowits contents, the creator, etc.
                            Make it short and sweet, but not too short so folks don&apos;t simply skip over it
                            entirely.
                        </Typography>
                        {/* {<div className={classes.heroButtons}>
                            <Grid container spacing={2} justify="center">
                                <Grid item>
                                    <Button variant="contained" color="primary">
                                        Main call to action
                                    </Button>
                                </Grid>
                                <Grid item>
                                    <Button variant="outlined" color="primary">
                                        Secondary action
                                    </Button>
                                </Grid>
                            </Grid>
                        </div>} */}
                    </Container>
                </div>
                <Container className={classes.cardGrid} maxWidth="lg">
                    <Grid container spacing={4}>
                        {Content.album.cards.map((card, index) => (
                            <Grid item key={card} xs={12} sm={6} md={4} key={index} >
                                <Card className={classes.card} onClick={() => { navigateTo(card.id) }}>
                                    <CardMedia className={classes.cardMedia}
                                        image={card.imageUrl}
                                        title="Image title" />
                                    <CardContent className={classes.cardContent}>
                                        <Typography gutterBottom variant="h5" component="h2">{card.caption}</Typography>
                                        <Typography>{card.description}</Typography>
                                        <Link to={`/gallery/${card.id}`}>More Info</Link>
                                    </CardContent>

                                </Card>
                            </Grid>
                        ))}
                    </Grid>
                </Container>
            </main>
        </React.Fragment>
    );
}