import React, { Component } from 'react'
import './Footer.css';
import Container from '@material-ui/core/Container';
import { Typography, Link } from '@material-ui/core';

export default class Footer extends Component {
    render() {
        return (
            <div>
                <hr></hr>
                <Container maxWidth="sm" className="footerbody">
                    <h3 className="font">Ever Safe Packers and Movers</h3>
                    <Typography></Typography>
                    <Typography variant="body2" color="textSecondary" align="center">
                        {"Copyright  "}
                        <Link color="inherit" href="https://eversafepacker.com/" className="copyrignt">
                            Ever Safe Packers and Movers | All Right Reserved
                        </Link>.&nbsp;
                        {new Date().getFullYear()}
                    </Typography>
                </Container>
            </div>
        )
    }
}
