import React, { Component } from 'react';
import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import Typography from '@material-ui/core/Typography';
import Footer from '../components/Footer/Footer';
import Axios from 'axios';
import './SignInSide.css'

class SignInSide extends Component {

    constructor(props) {
        super(props)

        this.state = {
            email: '',
            password: ''
        }
    }

    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }

    submitHandler = e => {
        e.preventDefault();
        console.log(JSON.stringify(this.state));
        Axios.post('http://localhost:7050/api/enquiry', this.state).then((response) => {
            console.log(JSON.stringify(response.data));
        }).catch(error => {
            alert('Please try later!');
        })
    }
    render() {

        const { email, password } = this.state;
        return (
            <Grid container component="main" className="root">
                <CssBaseline />
                <Grid item xs={false} sm={4} md={7} className="image" />
                <Grid item xs={12} sm={8} md={5} component={Paper} elevation={6} square>
                    <div className="paper">
                        <Avatar className="avatar"><LockOutlinedIcon /></Avatar>
                        <Typography component="h1" variant="h5">Sign in</Typography>
                        <form className="form" noValidate onSubmit={this.submitHandler}>
                            <TextField variant="outlined" margin="normal" required fullWidth id="email" label="Email Address" name="email" autoComplete="email" autoFocus value={email} onChange={this.changeHandler} />
                            <TextField variant="outlined" margin="normal" required fullWidth name="password" label="Password" type="password" id="password" autoComplete="current-password" value={password} onChange={this.changeHandler} />
                            <FormControlLabel control={<Checkbox value="remember" color="primary" />} label="Remember me" />
                            <Button type="submit" fullWidth variant="contained" color="primary" className="submit">Sign In</Button>
                        </form>
                    </div>
                    <Footer></Footer>
                </Grid>
            </Grid>
        );
    }
};
export default SignInSide;