import React from 'react';
import Footer from '../Footer/Footer';
import Header from './Header';
import Grid from "@material-ui/core/Grid";
import { makeStyles } from '@material-ui/core/styles';
import './Services.css';
import Paper from '@material-ui/core/Paper';
import Padding from '../utils/Padding';
import Navigation from '../Navigation/Navigation'
import { Container } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(2, 2),
        backgroundColor: '#f0f0f0'
    },
}));

export default function PaperSheet() {
    const classes = useStyles();
    return (
        <div>
            <Navigation></Navigation>
            <div className="service">
                <Header></Header>
                <Container maxWidth="xl">
                    <Grid container spacing={2} >
                        <Grid item xs={12} >
                            <h2 className="h2"> House Shifting</h2>
                            <Paper className={classes.root}>
                                <p component="p">Ever Safe Packers and Movers considers house shifting on demand and need. With top professionals from the packers and movers domain leading the team, our household goods relocation services are simply the best.</p>
                                <p component="p">From taking care of fragile crockeries, safeguarding your sofa or ensuring proper car shifting, our house shifting team knows how to handle every item reliably. When you want good house shifting service, we are the only packers and movers to look for.</p>
                            </Paper>
                        </Grid>
                        <Grid item xs={12} >
                            <h2 className="h2"> Office Relocation</h2>
                            <Paper className={classes.root}>
                                <p component="p">Ever Safe Packers and Movers gives an in-depth knowledge into the niche area of office relocation needs. Our experts know the nuances involved in packing and moving an office.</p>
                                <p component="p">From shifting servers, moving and storing office documents to relocating desks and chairs, we plan your office move according to your convenience. When you enter your new office, we will have everything ready and laid out for you in a planned way.</p>
                            </Paper>
                        </Grid>
                        <Grid item xs={12}>
                            <h2 className="h2"> Vehicle Moving</h2>
                            <Paper className={classes.root}>
                                <p component="p">Ever Safe Packers and Movers knows that home relocation is complete only when the family vehicle moves along. As a safe vehicle mover, our professional car and bike transport services will keep you smiling.</p>
                                <p component="p">We provide you a secure transport and transit for your vehicle. From bike transport to car shifting, we can assure you of a stress free shifting experience. When you want your car shifting done properly, we are here to help you.</p>
                            </Paper>
                        </Grid>
                    </Grid>
                </Container>
            </div>
            <Padding></Padding>
            <Footer></Footer>
        </div>

    );
}