import React, { Component } from 'react';
import './Order.css'
import axios from 'axios';
import { Container, TableHead, TableRow, TableCell, Table, TableBody } from '@material-ui/core';
import Navigation from '../Navigation/Navigation';


class Order extends Component {

    state = {
        orders: []
    }

    componentWillMount() {
        axios.get('http://localhost:7050/api/order').then((response) => {
            console.log(response);
            this.setState({
                orders: response.data.response
            })
        });
    }

    render() {

        let orders = this.state.orders.map((user) => {
            return (<tr key={user.orderId}>
                <TableCell >{user.orderId}</TableCell>
                <TableCell>{user.firstName}</TableCell>
                <TableCell>{user.lastName}</TableCell>
                <TableCell>{user.email}</TableCell>
                <TableCell>{user.mobile}</TableCell>
            </tr>)
        })
        return (
            <div>
                <Navigation />
                <Container maxWidth="xl">
                    <Table >
                        <TableHead className="orders">
                            <TableRow >
                                <TableCell >#</TableCell>
                                <TableCell >First Name</TableCell>
                                <TableCell >Last Name</TableCell>
                                <TableCell >Email Id</TableCell>
                                <TableCell >Mobile</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {orders}
                        </TableBody>
                    </Table>
                </Container>
            </div>
        )
    }
}

export default Order;
