import React, { Component } from 'react'
import { MDBContainer, MDBRow, MDBCol, MDBInput, MDBBtn } from 'mdbreact';
import { Container } from '@material-ui/core';

// const useStyles = makeStyles(theme => ({
//     root: {
//         padding: theme.spacing(2, 2),
//     },
//     how: {
//         color: 'gray',
//         textAlign: "center",
//     },
// }));

class NewSignIn extends Component {

    render() {
        return (
            <Container maxWidth="lg" className="">
                <div>
                    <MDBContainer>
                        <MDBRow>
                            <MDBCol md="6">
                                <form>
                                    <p className="h5 text-center mb-4">Sign in</p>
                                    <div className="grey-text">
                                        <MDBInput label="Type your email" icon="envelope" group type="email" validate error="wrong" success="right" />
                                        <MDBInput label="Type your password" icon="lock" group type="password" validate />
                                    </div>
                                    <div className="text-center">
                                        <MDBBtn>Login</MDBBtn>
                                    </div>
                                </form>
                            </MDBCol>
                        </MDBRow>
                    </MDBContainer>
                </div>
                <div>
                    <MDBContainer>
                        <MDBRow>
                            <MDBCol md="6">
                                <form>
                                    <p className="h4 text-center mb-4">Sign in</p>
                                    <label htmlFor="defaultFormLoginEmailEx" className="grey-text">Your email</label>
                                    <input type="email" id="defaultFormLoginEmailEx" className="form-control" />
                                    <br />
                                    <label htmlFor="defaultFormLoginPasswordEx" className="grey-text">Your password</label>
                                    <input type="password" id="defaultFormLoginPasswordEx" className="form-control" />
                                    <div className="text-center mt-4"><MDBBtn color="indigo" type="submit">Login</MDBBtn></div>
                                </form>
                            </MDBCol>
                        </MDBRow>
                    </MDBContainer>
                </div>
            </Container>
        )
    }
}

export default NewSignIn;
