import React from 'react';
import MaterialTable from 'material-table';
import Navigation from '../Navigation/Navigation';

export default function MaterialTableDemo() {


    const [state, setState] = React.useState({
        columns: [
            { title: 'First Name', field: 'firstName' },
            { title: 'Last Name', field: 'lastName' },
            { title: 'Email', field: 'email' },
            { title: 'Mobile', field: 'mobile' },
            {
                title: 'Gender',
                field: 'gender',
                lookup: { 1: 'Male', 2: 'Female' },
            },
        ],
        data: [
            {
                firstName: 'Shahzad',
                lastName: 'Hussain',
                email: 'shahzad.hssain@gmail.com',
                mobile: '7828588330',
                gender: 1
            },
            {
                firstName: 'Ejaz',
                lastName: 'Hussain',
                email: 'ejaz.hussain@gmail.com',
                mobile: '7828588330',
                gender: 2
            },
        ],
    });

    return (
        <div>
            <Navigation></Navigation>
            <MaterialTable
                title="Editable"
                columns={state.columns}
                data={state.data}
                editable={{
                    onRowAdd: newData =>
                        new Promise(resolve => {
                            setTimeout(() => {
                                resolve();
                                const data = [...state.data];
                                data.push(newData);
                                setState({ ...state, data });
                            }, 600);
                        }),
                    onRowUpdate: (newData, oldData) =>
                        new Promise(resolve => {
                            setTimeout(() => {
                                resolve();
                                const data = [...state.data];
                                data[data.indexOf(oldData)] = newData;
                                setState({ ...state, data });
                            }, 600);
                        }),
                    onRowDelete: oldData =>
                        new Promise(resolve => {
                            setTimeout(() => {
                                resolve();
                                const data = [...state.data];
                                data.splice(data.indexOf(oldData), 1);
                                setState({ ...state, data });
                            }, 600);
                        }),
                }}>
            </MaterialTable>
        </div>
    );
}