import React from 'react';
import './Tabs.css';
import { Link } from "react-router-dom";
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <Typography
            component="div"
            role="tabpanel"
            hidden={value !== index}
            id={`wrapped-tabpanel-${index}`}
            aria-labelledby={`wrapped-tab-${index}`}
            {...other}>
            <Box p={3}>{children}</Box>
        </Typography>
    );
}

TabPanel.propTypes = {
    children: PropTypes.node,
    index: PropTypes.any.isRequired,
    value: PropTypes.any.isRequired,
};

function a11yProps(index) {
    return {
        id: `wrapped-tab-${index}`,
        'aria-controls': `wrapped-tabpanel-${index}`,
    };
}

const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1,

        backgroundColor: theme.palette.background.paper,
    },
    base: {
        flexGrow: 1,
        backgroundColor: '#1d809f',
        // width: 'auto',
        alignItems: "center"
    },
    middle: {
        flexGrow: 1,
        marginLeft: 25,
        marginRight: 25
    }
}));

export default function TabsWrappedLabel() {
    const classes = useStyles();
    const [value, setValue] = React.useState('one');

    function handleChange(event, newValue) {
        setValue(newValue);
    }

    return (
        <div>
            <div>
                <nav>
                    <h2 className="logo">Ever Safe Packers and Movers</h2>
                    <ul>
                        <li> <Link to="/">Home</Link></li>
                        <li> <Link to="/about">About</Link></li>
                        <li> <Link to="/services">Services</Link></li>
                        <li> <Link to="/contact">Contact Us</Link></li>
                    </ul>
                </nav >
            </div>

            {/* <div className={classes.middle}>
                Hello Shahzad
            </div> */}
            <div className={classes.root}>
                <AppBar className={classes.base} position="static">
                    <Tabs value={value} onChange={handleChange} aria-label="wrapped label tabs example">
                        <Tab value="one" label="Tab1" wrapped {...a11yProps('one')} />
                        <Tab value="two" label="Tab2" {...a11yProps('two')} />
                        <Tab value="three" label="Tab3" {...a11yProps('three')} />
                    </Tabs>
                </AppBar>
                <TabPanel value={value} index="one">Item One</TabPanel>
                <TabPanel value={value} index="two">Item Two</TabPanel>
                <TabPanel value={value} index="three">Item Three</TabPanel>
            </div>
        </div>
    );
}