import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { Typography, Paper } from '@material-ui/core';

const useStyles = makeStyles(theme => ({
    body: {
        padding: '8px',
        margin: '8px',
        textAlign: "left",
        // minHeight: '100vh'
    },
    heading: {
        color: 'gray',
        fontSize: '20px',
        fontWeight: 'bold',
        textAlign: "left"
    }

}));

function OfficeAddress() {
    const classes = useStyles();
    return (
        <Paper>
            <div className={classes.body}>
                <Typography className={classes.heading}>Head Office</Typography>
                <p>Address: Cabin No-8, Second Floor Corner Market Malviye Nagar New Delhi-110017</p>
                <p>Phone: +91-7827628538,&nbsp;+91-9234567890</p>
                <p>Email Address: info@eversafepacker.com</p>
                Website: www.eversafepacker.com
            </div>
        </Paper>
    );
}
export default OfficeAddress;
