import React, { Component } from "react";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import { TextareaAutosize, Container } from "@material-ui/core";
import Axios from 'axios';
// import { useHistory } from 'react-router-dom';
import './InquiryForm.css';
import { Formik, Form } from 'formik';

class EnquiryForm extends Component {

    constructor(props) {
        super(props)
        this.state = {
            firstName: '',
            lastName: '',
            email: '',
            mobile: '',
            enquiry: ''
        }
    }

    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }

    submitHandler = e => {
        e.preventDefault();
        console.log(JSON.stringify(this.state));
        Axios.post('http://localhost:7050/api/enquiry', this.state).then((response) => {
            console.log(JSON.stringify(response.data));
            alert(response.data.message);
        }).catch(error => {
            alert('Please try later!');
            console.log(error);
        })
    }

    render() {
        const { firstName, lastName, email, mobile, enquiry } = this.state;
        return (
            <Container maxWidth="lg">
                <h1 className="enquiry">Drop Your Message</h1>
                <Formik
                    initialValues={{ firstName: '', lastName: '', email: '', mobile: '', enquiry: '' }}
                    validate={values => {
                        const errors = {};
                        if (!values.firstName) {
                            errors.firstName = 'First Name is Required';
                        }
                        else if (errors.firstName.length < 3) {
                            errors.firstName = 'E Name can not be less than 3 chararcter';
                        }
                        if (!values.lastName) {
                            errors.lastName = 'Last Name is Required';
                        }

                        if (!values.email) {
                            errors.email = 'Email is Required';
                        }
                        else if (
                            !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                        ) {
                            errors.email = 'Invalid email address';
                        }
                        return errors;
                    }}
                    onSubmit={(values, { setSubmitting }) => {
                        setTimeout(() => {
                            alert(JSON.stringify(values, null, 2));
                            Axios.post('http://localhost:7050/api/enquiry', values).then((response) => {
                                console.log(JSON.stringify(response.data));
                                alert(response.data.message);

                            }).catch(error => {
                                alert('Network problem! Try later');
                                console.log(error);
                            })
                            setSubmitting(false);
                        }, 400);
                    }}
                >{({ isSubmitting }) => (
                    <Form onSubmit={this.submitHandler}>
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    autoComplete="firstName"
                                    name="firstName"
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="firstName"
                                    label="First Name"
                                    placeholder="First Name"
                                    value={firstName} onChange={this.changeHandler}
                                />
                            </Grid>

                            <Grid item xs={12} sm={6}>
                                <TextField
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="lastName"
                                    label="Last Name"
                                    name="lastName"
                                    autoComplete="lastName"
                                    placeholder="Last Name"
                                    value={lastName} onChange={this.changeHandler}
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <TextField
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="email"
                                    label="Email Address"
                                    name="email"
                                    autoComplete="email"
                                    placeholder="Email"
                                    value={email} onChange={this.changeHandler}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="mobile"
                                    label="Mobile"
                                    name="mobile"
                                    autoComplete="mobile"
                                    placeholder="Mobile"
                                    value={mobile} onChange={this.changeHandler}
                                />
                            </Grid>

                            <Grid item xs={12}>
                                <TextareaAutosize className="textarea"
                                    name="enquiry"
                                    required
                                    fullWidth
                                    autoComplete="message"
                                    rows={5}
                                    placeholder="Type your message"
                                    label="Enquiry"
                                    value={enquiry} onChange={this.changeHandler}
                                />
                            </Grid>
                            <Button type="submit" fullWidth variant="contained" color="primary" className="submit" disabled={isSubmitting} >Send</Button>
                        </Grid>
                    </Form>
                )}
                </Formik>
            </Container>
        );
    }
}
export default EnquiryForm;
