import React, { Component } from 'react';

import axios from 'axios';
// import { Container, Table, TableHead } from '@material-ui/core';

import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Navigation from '../Navigation/Navigation';
import { Container } from '@material-ui/core';


class EnquiryList extends Component {

    state = {
        enquiries: []
    }

    componentWillMount() {
        axios.get('http://localhost:7050/api/enquiry').then((response) => {
            console.log('====================================');
            console.log(response);
            console.log('====================================');
            this.setState({
                enquiries: response.data.response
            })
        });
    }

    render() {

        let enquiries = this.state.enquiries.map((enquiry) => {
            return (<tr key={enquiry.enquiryId}>
                <th >{enquiry.enquiryId}</th>
                <td>{enquiry.firstName}</td>
                <td>{enquiry.lastName}</td>
                <td>{enquiry.email}</td>
                <td>{enquiry.mobile}</td>
                <td>{enquiry.enquiry}</td>
                <td>{enquiry.date}</td>
            </tr>)
        })
        return (
            <div>
                <Navigation />
                <Container maxWidth="xl">
                    <Table >
                        <TableHead>
                            <TableRow>
                                <TableCell >#Id</TableCell>
                                <TableCell >First Name</TableCell>
                                <TableCell >Last Name</TableCell>
                                <TableCell >Email Id</TableCell>
                                <TableCell >Mobile</TableCell>
                                <TableCell >Message</TableCell>
                                <TableCell >Date</TableCell>
                            </TableRow>
                        </TableHead>
                        <TableBody>
                            {enquiries}
                        </TableBody>
                    </Table>
                </Container>

            </div>
        )
    }
}

export default EnquiryList;
