import React from "react";
import Content from "../../../content"
import { CssBaseline, makeStyles, Container } from "@material-ui/core";
import Navigation from "../../Navigation/Navigation";
import Footer from "../../Footer/Footer";


const useStyles = makeStyles(theme => ({

    gallery: {
        // paddingTop: '10%',
        minHeight: '100vh'
    }

}));

const PhotoDetail = (props) => {
    console.log(props)
    const filteredPhoto = Content.album.cards.filter((photo) => {
        return photo.id === parseInt(props.match.params.photoId)
    })
    return (

        <React.Fragment>
            <CssBaseline />
            <div>
                <Navigation></Navigation>
                <div className="gallery">
                    {
                        filteredPhoto.length > 0 ?
                            <div>
                                <h2> {filteredPhoto[0].caption}</h2>
                                <img src={filteredPhoto[0].imageUrl} width="800" height="500" />
                                <p><strong> {filteredPhoto[0].description}</strong></p>
                            </div>
                            : <p>No image available</p>
                    }
                </div>
                <Footer></Footer>
            </div>
        </React.Fragment>


    )
}

export default PhotoDetail;