import React, { Component, useEffect, useReducer, useState } from 'react'
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import './Booking.css'
import Calendar from "../utils/Calender";
import Axios from "axios";
import { Formik, Form } from 'formik';

class Booking extends Component {

    constructor(props) {
        super(props)

        this.state = {
            firstName: '',
            lastName: '',
            email: '',
            mobile: '',
            from: {
                fromAddressline1: '',
                fromAddressline2: '',
                fromState: '',
                fromCity: '',
                fromLandmark: '',
                fromPincode: '',
            },
            to: {
                toAddressline1: '',
                toAddressline2: '',
                toState: '',
                toCity: '',
                toLandmark: '',
                toPincode: ''
            }
        }
    }


    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }

    submitHandler = e => {
        e.preventDefault();
        console.log(JSON.stringify(this.state));
        Axios.post('http://localhost:7050/api/enquiry', this.state).then((response) => {
            console.log(JSON.stringify(response.data));
            alert(response.data.message);
        }).catch(error => {
            alert('Please try later!');
            console.log(error);
        })
    }

    render() {
        const { firstName, lastName, email, mobile, fromAddressline1, fromAddressline2, fromState, fromCity, fromLandmark, fromPincode, toAddressline1, toAddressline2, toState, toCity, toLandmark, toPincode } = this.state;
        return (
            <Formik
                initialValues={{ firstName: '', lastName: '', email: '', mobile: '', fromAddressline1: '', fromAddressline2: '', fromState: '', fromCity: '', fromPincode: '', toAddressline1: '', toAddressline2: '', toState: '', toCity: '', toPincode, fromLandmark: '', toLandmark: '' }}
                validate={values => {
                    const errors = {};
                    if (!values.firstName) {
                        errors.firstName = 'First Name is Required';
                    }
                    else if (errors.firstName.length < 3) {
                        errors.firstName = 'First Name can not be less than 3 chararcter';
                    }
                    if (!values.lastName) {
                        errors.lastName = 'Last Name is Required';
                    }

                    if (!values.email) {
                        errors.email = 'Email is Required';
                    }
                    else if (
                        !/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)
                    ) {
                        errors.email = 'Invalid email address';
                    }
                    return errors;
                }}
                onSubmit={(values, { setSubmitting }) => {
                    setTimeout(() => {
                        alert(JSON.stringify(values, null, 2));
                        Axios.post('http://localhost:7050/api/enquiry', values).then((response) => {
                            console.log(JSON.stringify(response.data));
                            console.log('====================================');
                            alert(response.data.message);
                        }).catch(error => {
                            console.log('====================================');
                            alert('Network problem! Try later');
                            console.log(error);
                        })
                        setSubmitting(false);
                    }, 400);
                }}
            >{({ isSubmitting }) => (
                <Form onSubmit={this.submitHandler}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                autoComplete="firstName"
                                name="firstName"
                                variant="outlined"
                                required
                                fullWidth
                                id="firstName"
                                label="First Name"
                                placeholder="First Name"
                                value={firstName} onChange={this.changeHandler}
                            />
                        </Grid>

                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="lastName"
                                label="Last Name"
                                name="lastName"
                                autoComplete="lastName"
                                placeholder="Last Name"
                                value={lastName} onChange={this.changeHandler}
                            />
                        </Grid>

                        <Grid item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="email"
                                label="Email Address"
                                name="email"
                                autoComplete="email"
                                placeholder="Email"
                                value={email} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="mobile"
                                label="Mobile"
                                name="mobile"
                                autoComplete="mobile"
                                placeholder="Mobile"
                                value={mobile} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} className="address">From Address </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Address Line 1"
                                name="fromAddressline1"
                                autoComplete="fromAddressline1"
                                placeholder="Address Line1"
                                value={fromAddressline1} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Address Line 2"
                                name="fromAddressline2"
                                autoComplete="fromAddressline2"
                                placeholder="Address Line2"
                                value={fromAddressline2} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="State"
                                name="fromState"
                                autoComplete="fromState"
                                placeholder="State"
                                value={fromState} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="City"
                                name="fromCity"
                                autoComplete="fromCity"
                                placeholder="City"
                                value={fromCity} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Landmark/Locality"
                                name="fromLandmark"
                                autoComplete="fromLandmark"
                                placeholder="Landmark"
                                value={fromLandmark} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Pincode"
                                name="fromPincode"
                                autoComplete="fromPincode"
                                placeholder="Pincode"
                                value={fromPincode} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} className="address">To Address </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Address Line 1"
                                name="toAddressline1"
                                autoComplete="toAddressline1"
                                placeholder="Address Line1"
                                value={toAddressline1} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Address Line 2"
                                name="toAddressline2"
                                autoComplete="toAddressline2"
                                placeholder="Address Line2"
                                value={toAddressline2} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="State"
                                name="toState"
                                autoComplete="toState"
                                placeholder="State"
                                value={toState} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="City"
                                name="toCity"
                                autoComplete="toCity"
                                placeholder="City"
                                value={toCity} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Landmark/Locality"
                                name="toLandmark"
                                autoComplete="toLandmark"
                                placeholder="Landmark"
                                value={toLandmark} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                label="Pincode"
                                name="toPincode"
                                autoComplete="toPincode"
                                placeholder="Pincode "
                                value={toPincode} onChange={this.changeHandler}
                            />
                        </Grid>
                        <Grid item xs={12} >
                            <Calendar ></Calendar>
                        </Grid>

                        <Button type="submit" fullWidth variant="contained" color="primary" disabled={isSubmitting}>Book Now</Button>
                    </Grid>
                </Form>
            )}
            </Formik>
        )
    }
}

export default Booking
