import React from 'react';
import MaterialTable from 'material-table';
import { Icon, makeStyles } from '@material-ui/core';


const useStyles = makeStyles({
    root: {
        width: '100%',
    },
    tableWrapper: {
        maxHeight: 800,
        overflow: 'auto',
    },
});

export default function MaterialTableDemo() {

    const classes = useStyles();
    const [state, setState] = React.useState({
        columns: [
            { title: 'Name', field: 'name' },
            { title: 'Surname', field: 'surname' },
            { title: 'Birth Year', field: 'birthYear', type: 'numeric' },
            {
                title: 'Birth Place', field: 'birthCity', lookup: {
                    34: 'Sahebganj', 63: 'Muzaffarpur'
                },
            },
        ],
        data: [
            {
                name: 'Shahid', surname: 'Hussain', birthYear: 1962, birthCity: 63
            },
            {
                name: 'Ejaz', surname: 'Hussain', birthYear: 1994, birthCity: 34,
            },
            {
                name: 'Sahil', surname: 'Hussain', birthYear: 2003, birthCity: 34,
            },
            {
                name: 'Shahzad', surname: 'Hussain', birthYear: 1992, birthCity: 63
            },
            {
                name: 'Hariom', surname: 'Singh', birthYear: 1980, birthCity: 34,
            },
            {
                name: 'Alok', surname: 'Kumar', birthYear: 1980, birthCity: 34,
            },
            {
                name: 'Sujeet', surname: 'Kumar', birthYear: 2003, birthCity: 34,
            },
            {
                name: 'Amit', surname: 'Kumar', birthYear: 2003, birthCity: 34,
            },
        ],
    });

    return (
        <div className={classes.root}>
            <MaterialTable
                title="Editable Example"
                columns={state.columns}
                data={state.data}
                editable={{
                    onRowAdd: newData =>
                        new Promise(resolve => {
                            setTimeout(() => {
                                resolve();
                                const data = [...state.data];
                                data.push(newData);
                                setState({ ...state, data });
                            }, 600);
                        }),
                    onRowUpdate: (newData, oldData) =>
                        new Promise(resolve => {
                            setTimeout(() => {
                                resolve();
                                const data = [...state.data];
                                data[data.indexOf(oldData)] = newData;
                                setState({ ...state, data });
                            }, 600);
                        }),
                    onRowDelete: oldData =>
                        new Promise(resolve => {
                            setTimeout(() => {
                                resolve();
                                const data = [...state.data];
                                data.splice(data.indexOf(oldData), 1);
                                setState({ ...state, data });
                            }, 600);
                        }),
                }}
            >

                <Icon>add_circle</Icon>

            </MaterialTable>
        </div>
    );
}