import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Navigation from '../Navigation/Navigation';
import './ContactUs.css'
import Footer from "../Footer/Footer";
import { Container, CssBaseline } from '@material-ui/core';
import EnquiryForm from '../EnquiryForm/InquiryForm';
import OfficeAddress from '../OfficeAddress/OfficeAddress';



const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(3, 2),
    },
    support: {
        // marginTop: '10px',
        // marginBottom: '10px',
        // padding: '5px',
        // margin: '5px',
    },
    heroContent: {
        backgroundColor: theme.palette.background.paper,
        padding: theme.spacing(8, 0, 6),
    },
}));

function ContactUs() {
    const classes = useStyles();
    return (
        <div>
            <CssBaseline />
            <Navigation></Navigation>
            <div className="footer">
                <Container maxWidth="lg" className={classes.support}>
                    <Typography variant="h5" align="left" color="textSecondary">Contact us on given details</Typography>
                </Container>

                <Container maxWidth="lg">
                    <Paper className={classes.root}>
                        <Typography component="p">
                            If you have any query please email us at <u><b>info@eversafepacker.com</b></u> Or Call on +91-7827628538
                        </Typography>
                    </Paper>
                    <OfficeAddress></OfficeAddress>
                </Container>
                <EnquiryForm></EnquiryForm>
            </div>
            <Footer></Footer>
        </div>
    )
}
export default ContactUs;

