import React, { Component } from 'react'
import Button from "@material-ui/core/Button";
import Grid from "@material-ui/core/Grid";
import TextField from "@material-ui/core/TextField";
import Axios from "axios";
import './Help.css';
import { Formik, Form } from 'formik';
import { Container } from '@material-ui/core';
import Navigation from '../Navigation/Navigation';
import { Typography } from '@material-ui/core';

class Help extends Component {

    constructor(props) {
        super(props)

        this.state = {
            mobile: ''
        }
    }

    changeHandler = (e) => {
        this.setState({ [e.target.name]: e.target.value })
    }

    submitHandler = e => {
        e.preventDefault();
        console.log(JSON.stringify(this.state));
        Axios.post('http://localhost:7050/api/enquiry', this.state).then((response) => {
            console.log(JSON.stringify(response.data));
            alert(response.data.message);
        }).catch(error => {
            alert('Please try later!');
            console.log(error);
        })
    }

    render() {
        const { mobile } = this.state;
        return (

            <div>
                <Navigation></Navigation>

                <div className="body">
                    <Container maxWidth="md" >
                        <Typography className="status">Check your booking status</Typography>
                        <Formik
                            initialValues={{ mobile: '' }}
                            onSubmit={(values, { setSubmitting }) => {
                                setTimeout(() => {
                                    alert(JSON.stringify(values, null, 2));
                                    Axios.post('http://localhost:7050/api/enquiry', values).then((response) => {
                                        console.log(JSON.stringify(response.data));
                                        console.log('====================================');
                                        alert(response.data.message);
                                    }).catch(error => {
                                        console.log('====================================');
                                        alert('Network problem! Try later');
                                        console.log(error);
                                    })
                                    setSubmitting(false);
                                }, 400);
                            }}
                        >{({ isSubmitting }) => (
                            <Form onSubmit={this.submitHandler} className="container-width">
                                <Grid container spacing={2}>

                                    <Grid item xs={12}>
                                        <TextField
                                            variant="outlined"
                                            required
                                            fullWidth
                                            id="mobile"
                                            label="Mobile"
                                            name="mobile"
                                            autoComplete="mobile"
                                            placeholder="Mobile"
                                            value={mobile} onChange={this.changeHandler}
                                        />
                                    </Grid>

                                    <Button type="submit" fullWidth variant="contained" color="primary" disabled={isSubmitting}>Check Status</Button>
                                </Grid>
                            </Form>
                        )}
                        </Formik>
                    </Container>

                </div>
            </div>
        )
    }
}

export default Help
